////
/// Headings
/// @group elements
////

// Simple default styles for headings 1 through 6. Anything more opinionated
// than simple font-size changes should likely be applied via classes
//
// @group headings
// @link http://csswizardry.com/2016/02/managing-typography-on-large-apps/

$font-size-h1:  36px !default;
$font-size-h2:  28px !default;
$font-size-h3:  24px !default;
$font-size-h4:  20px !default;
$font-size-h5:  18px !default;
$font-size-h6:  16px !default;

h1 {
  @include FontSize($font-size-h1, none);
}
h2 {
  @include FontSize($font-size-h2);
}
h3 {
  @include FontSize($font-size-h3);
}
h4 {
  @include FontSize($font-size-h4);
}
h5 {
  @include FontSize($font-size-h5);
}
h6 {
  @include FontSize($font-size-h6);
}
